	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Wine School","date":"16 Jan 2007","slug":"/2007-01-16-wine-school/","tags":"Y2007"},"html":"<p>The magazine <a href=\"http://www.stern.de/\">Stern</a> is publishing a 24-week long <a href=\"http://www.stern.de/weinschule\">wine course</a>, offering a <a href=\"http://www.rindchen.de/main.php?typ=K&amp;seite=sternweinschule\">package of 24 wines</a> by post from Rindchen with an article in the print magazine and an online forum. According to <a href=\"http://drinktank.blogg.de/\">The Drink Tank</a> (hosted by my employer at <a href=\"http://blogg.de/\">blogg.de</a>) over <a href=\"http://drinktank.blogg.de/eintrag.php?id=1682\">6000 packages have been shipped</a>, including one to yours truly. I'm a little late to the game. I just opened <a href=\"http://www.stern.de/lifestyle/kueche/getraenke/579959.html?eid=580464\">wine number one</a> and this is already week 3. I have a feeling this is not the kind of class where you want to catch up on your studies all at once (especially since MamaMaus is a beer drinker, therefore I have to study alone). As for the week one Bordeaux, the <a href=\"http://www.stern.de/lifestyle/kueche/getraenke/579959.html?eid=580464\">2005 Camarsac</a> is nice for under 10 Euros, but I don't smell the berries, and certainly not the cougar cage.</p>\n<p>Nice marketing gag... we usually don't read Stern but now we (or least I) will be reading every week until mid-June for sure.</p>"},"next":{"fields":{"title":"Wii Don't","date":"16 Jan 2007","slug":"/2007-01-16-wii-dont/"}},"previous":{"fields":{"title":"Stormy Weather","date":"17 Jan 2007","slug":"/2007-01-17-stormy-weather/"}}},{"node":{"fields":{"title":"Wii Don't","date":"16 Jan 2007","slug":"/2007-01-16-wii-dont/","tags":"Y2007"},"html":"<p>Unlike <a href=\"http://lumma.de/index.php?tags=wii\">my boss</a> and just about everyone else at NU2M, I don't own a Nintendo <a href=\"http://wii.nintendo.com/\">Wii</a>. I have not ordered a Wii. I have absolutely no interest in getting a Wii.</p>\n<p>I may be in danger of losing my job.</p>"},"next":{"fields":{"title":"Court Appointment","date":"16 Jan 2007","slug":"/2007-01-16-court-appointment/"}},"previous":{"fields":{"title":"Wine School","date":"16 Jan 2007","slug":"/2007-01-16-wine-school/"}}},{"node":{"fields":{"title":"Court Appointment","date":"16 Jan 2007","slug":"/2007-01-16-court-appointment/","tags":"Y2007"},"html":"<p>The <a href=\"http://www.shopblogger.de/blog/\">Shopblogger</a> writes about the trials and tribulations of running a supermarket in Germany and is one of the more popular German blogs. Today he explains about a <a href=\"http://www.shopblogger.de/blog/archives/4651-Gerichtstermin.html\">hearing in employment court</a> where basically an ex-employee was awarded back pay and vacation for hours she never worked. German employment law can be absurd (firing an employee is technically illegal, you can always be taken to court), and is the main reason MamaMaus no longer has a blog of her own (yes, she used to) and no longer wants to be mentioned here by name.</p>"},"next":{"fields":{"title":"Support Our Son's Habit","date":"12 Jan 2007","slug":"/2007-01-12-support-our-sons-habit/"}},"previous":{"fields":{"title":"Wii Don't","date":"16 Jan 2007","slug":"/2007-01-16-wii-dont/"}}}],"pathPrefix":"","first":false,"last":false,"index":163,"pageCount":1101}}

